import { useEffect, useRef, useState } from 'react';

export function useIframeMessenger({ onMessageReceived, targetOrigin = '*' }) {
  const iframeRef = useRef(null);
  const [isIframeReady, setIsIframeReady] = useState(false);
  const [lastMessage, setLastMessage] = useState(null);

  // Send message to child iframe
  const sendMessage = (message) => {
    if (!iframeRef.current || !isIframeReady) {
      console.warn('Iframe not ready or not available');
      return;
    }

    try {
      iframeRef.current.contentWindow.postMessage(message, targetOrigin);
    } catch (error) {
      console.error('Failed to post message to iframe:', error);
    }
  };

  // Handle incoming messages
  useEffect(() => {
    const handleMessage = (event) => {
      // Security checks
      if (targetOrigin !== '*' && event.origin !== targetOrigin) return;
      if (iframeRef.current && event.source !== iframeRef.current.contentWindow) return;

      setLastMessage(event.data);
      if (onMessageReceived) {
        onMessageReceived(event.data);
      }
    };
    if(typeof window !== 'undefined') {
        window.addEventListener('message', handleMessage);
        return () => window.removeEventListener('message', handleMessage);
    }
  }, [onMessageReceived, targetOrigin]);

  // Handle iframe load event
  const handleIframeLoad = () => {
    setIsIframeReady(true);
  };

  return {
    iframeRef,
    isIframeReady,
    lastMessage,
    sendMessage,
    iframeProps: {
      ref: iframeRef,
      onLoad: handleIframeLoad,
      // Add other default iframe props if needed
    }
  };
}