import React, { useState, useEffect, useRef } from 'react';
import Layout from '../../components/Layout';
import {
  Box,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  ToggleButton,
  ToggleButtonGroup,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
} from '@mui/material';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import { SnackbarAlert } from '../../components/placeholder/SnackbarAlert';
import { PromptSection } from '../../components/placeholder/PromptSection';
import { basicHTMLApp } from '../../helpers/git';
import LoginModal from '../../components/LoginModal';
import Parse from '../../vendor/Parse';
import Editor from '@monaco-editor/react';
import Frame from 'react-frame-component';
import { useIframeMessenger } from '../../hooks/useIframeMessenger';
import { DEFAULT_PROMPT } from '../../helpers/constants';

const AppPage = ({ location, placeholder }) => {
  const [state, setState] = useState({
    appName: 'My Web App',
    prompt: '',
    enhancedPrompt: '',
    files: {
      'index.html': basicHTMLApp,
    },
    snackbarOpen: false,
    snackbarMessage: '',
    snackbarSeverity: 'info',
    systemPrompt: DEFAULT_PROMPT,
    showCodeEditor: true,
    showAppOutput: true,
    showAI: true,
    loginModalOpen: false,
    user: null,
    isAppDeployed: false,
    deployModalOpen: false,
    isDeploying: false,
    deployStatusMessage: '',
    isPublishing: false,
    publishModalOpen: false,
    src: placeholder,
    dest: '',
    token: '',
    previewMode: 'live',
    categories: [
      'Developer Tools',
      'Education',
      'Productivity',
      'Entertainment',
      'Social Media',
      'E-commerce',
      'Health & Fitness',
      'Finance',
      'Other'
    ],
    selectedCategory: '',
    release: 0,  // New field to track release status
  });

  const [frameLoaded, setFrameLoaded] = useState(false);
  const [previewUrl, setPreviewUrl] = useState(`https://appseek.ai/build/${placeholder}`);
  const [previewUrlInput, setPreviewUrlInput] = useState(`https://appseek.ai/build/${placeholder}`);

  const {
    appName,
    prompt,
    enhancedPrompt,
    files,
    snackbarOpen,
    snackbarMessage,
    snackbarSeverity,
    systemPrompt,
    loginModalOpen,
    user,
    isAppDeployed,
    deployModalOpen,
    isDeploying,
    deployStatusMessage,
    isPublishing,
    publishModalOpen,
    src,
    token,
    previewMode,
    categories,
    selectedCategory,
    release,  // Destructure the release status
  } = state;

  const frameRef = useRef(null);

  const handleChildMessage = (message) => {
    console.log('Received from child:', message);
    if (message.type === 'deeplink' && typeof message.data === 'object') {
      const params = new URLSearchParams(message.data).toString();
      const newUrl = `${previewUrl.split('?')[0]}?${params}`;
      setPreviewUrlInput(newUrl);
      setPreviewUrl(newUrl);
    }
  };

  const { iframeRef, sendMessage } = useIframeMessenger({
    onMessageReceived: handleChildMessage,
    targetOrigin: typeof window !== 'undefined' && window.location.origin,
  });

  useEffect(() => {
    const combinedCode = Object.values(files).join('\n\n');
    const titleMatch = files['index.html'].match(/<title>(.*?)<\/title>/i);
    const newAppName = titleMatch ? titleMatch[1].trim() : 'My Web App';

    setState((prev) => ({
      ...prev,
      enhancedPrompt: `${combinedCode}\n\n${prompt}\n\n${systemPrompt}`,
      appName: newAppName,
    }));
  }, [files, prompt, systemPrompt]);

  const queryParams = new URLSearchParams(location.search);
  const urlPrompt = queryParams.get('prompt');

  const fetchAppData = async () => {
    try {
        const result = await Parse.Cloud.run('getApp', { id: placeholder });

        // Normalize the response to match expected state structure
        const normalizedFiles = result.exists
            ? { 'index.html': result.content } 
            : { 'index.html': basicHTMLApp };

        setState((prev) => ({
            ...prev,
            files: normalizedFiles,
            isAppDeployed: result.exists,
            appName: result.exists ? (result.content.match(/<title>(.*?)<\/title>/i)?.[1].trim() || 'My Web App') : 'My Web App',
            selectedCategory: result.category || '',  // Assuming you handle category as well
            release: result.release || 0,
        }));
    } catch (error) {
        console.error('Error fetching app data:', error);
        setState((prev) => ({
            ...prev,
            isAppDeployed: false,
            files: { 'index.html': basicHTMLApp },
            snackbarMessage: 'Failed to load app data',
            snackbarSeverity: 'error',
            snackbarOpen: true,
        }));
    }
};


  useEffect(() => {
    fetchAppData();
  }, [placeholder]);

  useEffect(() => {
    const combinedCode = Object.values(files).join('\n\n');
    setState((prev) => ({
      ...prev,
      enhancedPrompt: `${combinedCode}\n\n${prompt}\n\n${systemPrompt}`,
    }));
  }, [files, prompt, systemPrompt]);

  useEffect(() => {
    const currentUser = Parse.User.current();
    if (currentUser) setState((prev) => ({ ...prev, user: currentUser }));
  }, []);

  useEffect(() => {
    if (urlPrompt) {
      setState((prev) => ({ ...prev, systemPrompt: urlPrompt }));
    }
  }, [urlPrompt]);

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(enhancedPrompt).then(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Prompt copied to clipboard!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
      }));
    }).catch(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to copy prompt',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
    });
  };

  const handleDeploy = async () => {
    try {
      await Parse.Cloud.run('deployApp', {
        files,
        placeholder,
        appname: appName.trim(),
      });
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App deployed successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isAppDeployed: true,
        deployStatusMessage: 'A link will appear beside the app name',
        isDeploying: false,
      }));
      setTimeout(() => setState((prev) => ({ ...prev, deployStatusMessage: '' })), 3000);
    } catch {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to deploy app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
        isDeploying: false,
      }));
    }
  };

  const handlePublish = async () => {
    try {
      await Parse.Cloud.run('publishApp', {
        placeholder: src,
        category: selectedCategory,
        appname: appName.trim()
      });
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App published successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isPublishing: false,
      }));
    } catch {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to publish app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
        isPublishing: false,
      }));
    }
  };

  const handleFilesChange = (filePath, newCode) => {
    setState((prev) => ({
      ...prev,
      files: { ...prev.files, [filePath]: newCode },
    }));
  };

  const handleBookmarkSave = () => {
    // Check if the browser supports the bookmark API
    if (window.sidebar && window.sidebar.addPanel) {
      // Firefox
      window.sidebar.addPanel(appName, previewUrlInput, "");
    } else if (window.external && ('AddFavorite' in window.external)) {
      // IE
      window.external.AddFavorite(previewUrlInput, appName);
    } else if (window.opera && window.print) {
      // Opera
      const bookmark = document.createElement('a');
      bookmark.href = previewUrlInput;
      bookmark.title = appName;
      bookmark.rel = 'sidebar';
      bookmark.click();
    } else {
      // Other browsers - show a prompt
      setState(prev => ({
        ...prev,
        snackbarMessage: `Press ${navigator.userAgent.includes('Mac') ? 'Cmd+D' : 'Ctrl+D'} to bookmark this page`,
        snackbarSeverity: 'info',
        snackbarOpen: true,
      }));
    }
  };

  const generateIframeContent = () => {
    const { 'index.html': html = '' } = files;
    return `<!DOCTYPE html><html><body>${html}</body></html>`;
  };

  return (
    <Layout deployButton={
  <Box sx={{ display: 'flex', gap: 1 }}>
    <Button
      variant="contained"
      onClick={() => setState(prev => ({ ...prev, deployModalOpen: true }))}
    >
      {isAppDeployed ? 'Update' : 'Create'}  {/* Show "Update" if app is deployed */}
    </Button>
    <Button
      variant="contained"
      color="secondary"
      onClick={() => setState(prev => ({ ...prev, publishModalOpen: true }))}
    >
      Publish
    </Button>
  </Box>
    }>
      <Typography variant="h5" sx={{ mt: 2, mb: 3, fontWeight: 600 }}>{appName}</Typography>

      <ToggleButtonGroup
        value={previewMode}
        exclusive
        onChange={(e, val) => val && setState((prev) => ({ ...prev, previewMode: val }))}
        sx={{ mb: 2 }}
      >
        <ToggleButton value="release">
          {release === 0 ? 'New App' : `Release Version ${release}`}
        </ToggleButton>
      </ToggleButtonGroup>

      <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 2, mb: 4, height: 'calc(100vh - 250px)' }}>
        <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          <Editor
            height="100%"
            width="100%"
            theme="vs-light"
            defaultLanguage="html"
            value={files['index.html']}
            onChange={(value) => handleFilesChange('index.html', value)}
            options={{ minimap: { enabled: false }, fontSize: 14 }}
          />
        </Box>
        <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', p: 1, backgroundColor: '#f5f5f5' }}>
            <TextField
              fullWidth
              variant="outlined"
              size="small"
              value={previewUrlInput}
              onChange={(e) => setPreviewUrlInput(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && setPreviewUrl(previewUrlInput)}
            />
            <Button
              variant="contained"
              onClick={handleBookmarkSave}
              sx={{ minWidth: '40px', px: 1 }}
            >
              <BookmarkIcon />
            </Button>
            <Button
              variant="outlined"
              onClick={() => window.open(previewUrlInput, '_blank')}
              sx={{ minWidth: '40px', px: 1 }}
            >
              <OpenInNewIcon />
            </Button>
          </Box>
          <Frame
            ref={frameRef}
            style={{ width: '100%', height: '100%', border: 'none' }}
            initialContent={generateIframeContent()}
            onLoad={() => setFrameLoaded(true)}
            mountTarget="#mount-target"
          >
            {frameLoaded && <div id="mount-target" />}
          </Frame>
        </Box>
      </Box>

      <PromptSection
        prompt={prompt}
        setPrompt={(value) => setState((prev) => ({ ...prev, prompt: value }))}
        enhancedPrompt={enhancedPrompt}
        handleCopyPrompt={handleCopyPrompt}
        systemPrompt={systemPrompt}
        setSystemPrompt={(value) => setState((prev) => ({ ...prev, systemPrompt: value }))}
      />

      <LoginModal
        open={loginModalOpen}
        onClose={() => setState((prev) => ({ ...prev, loginModalOpen: false }))}
        onLoginSuccess={() => {
          const currentUser = Parse.User.current();
          setState((prev) => ({ ...prev, user: currentUser, loginModalOpen: false }));
        }}
      />

      <Dialog open={deployModalOpen} onClose={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>
        <DialogTitle>Create Your App</DialogTitle>
        <DialogContent>
          <DialogContentText>
            This will make your app available at a public URL.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>
            Cancel
          </Button>
          <Button
            onClick={() => {
              setState((prev) => ({
                ...prev,
                isDeploying: true,
                deployModalOpen: false
              }));
              handleDeploy();
            }}
            color="primary"
            variant="contained"
            disabled={isDeploying}
          >
            {isDeploying ? 'Deploying...' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={publishModalOpen} onClose={() => setState((prev) => ({ ...prev, publishModalOpen: false }))}>
        <DialogTitle>Publish Your App</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Publish your app to make it discoverable in the app directory.
          </DialogContentText>
          <TextField
            autoFocus
            fullWidth
            label="App Name"
            value={appName}
            onChange={(e) => setState((prev) => ({ ...prev, appName: e.target.value }))}
          />
          <FormControl fullWidth sx={{ mt: 2 }}>
            <InputLabel>Category</InputLabel>
            <Select
              value={selectedCategory}
              label="Category"
              onChange={(e) => setState((prev) => ({ ...prev, selectedCategory: e.target.value }))}
            >
              {categories.map((category, index) => (
                <MenuItem key={index} value={category}>{category}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setState((prev) => ({ ...prev, publishModalOpen: false }))}>
            Cancel
          </Button>
          <Button
            onClick={() => {
              setState((prev) => ({ ...prev, isPublishing: true, publishModalOpen: false }));
              handlePublish();
            }}
            color="primary"
            variant="contained"
            disabled={isPublishing}
          >
            {isPublishing ? 'Publishing...' : 'Publish'}
          </Button>
        </DialogActions>
      </Dialog>

      <SnackbarAlert
        open={snackbarOpen}
        message={snackbarMessage}
        severity={snackbarSeverity}
        onClose={() => setState((prev) => ({ ...prev, snackbarOpen: false }))}
      />
    </Layout>
  );
};

export default AppPage;
